<script>
	export default {
		data() {
			return {
				defaultCover: '../../static/default_cover.jpeg',
				footSize: this.$footFontSize,
				reviewClassList: []
			}
		},
		onLoad(option) {
			let projectId = option.projectId
			let that = this
			this.$apis.postReviewClass({"projectId":projectId}).then(res => {
				if res.code == 200 {
					res.rows.forEach((row) => {
						if (row.bannerImg) {
							row.bannerImg = that.$config.aliYunEndpoint + row.bannerImg
						}
						that.reviewClassList.push(row)
					})
				} else {
					uni.showToast({
						title: "查询失败",
						icon: fail
					})
				}
			})
		},
		methods: {
			imageError(index, type) {
				this.reviewClassList[index]["bannerImg"] = this.defaultCover
			},
			beginTest(classid, title) {
				console.log(e.currentTarget.dataset.classid)
				uni.navigateTo({
					url: '/pages/questions/questions?classId='+classid + "&title=" + title
				})
			},
			//获取用户授权  匿名数据
			getUserInfo(e) {
				if (e.detail.errMsg == 'getUserInfo:ok') {
					//对数据进行操作
					console.log(e)
				} else {
					this.$operate.toast({
						title: '授权失败无法登录！'
					})
				}
			},
			// 获取用户信息  后期放弃
			userInfo() {
				uni.login({
					provider: 'weixin',
					success(login) {
						console.log(login);
						uni.getUserInfo({
							provider: 'weixin',
							lang: 'zh_CN',
							success(user) {
								console.log(user);
							}
						})
					}
				})
			},
			//获取用户手机号
			getUserPhone(e) {
				// 获取code 小程序专有，用户登录凭证。
				uni.login({
					provider: 'weixin',
					success(login) {
						console.log(login);
					}
				})
				console.log(e)
				//手机号加密数据
				if (e.detail.errMsg == 'getPhoneNumber:ok') {
					console.log(e)
					//传给后端的参数
					let data = {
						session_key: '后端返回微信 openid ',
						encryptedData: e.detail.encryptedData,
						iv: e.detail.iv,
					}
					
				} else {
					this.$operate.toast({
						title: '授权失败无法登录！'
					})
				}
			},
			//最新登录测试
			goLogin() {
				// 获取code 小程序专有，用户登录凭证。
				uni.login({
					provider: 'weixin',
					success(login) {
						console.log(login);
					}
				})
				 // desc: '用于完善会员资料'  必填 声明获取用户个人信息后的用途，后续会展示在弹窗中
				uni.getUserProfile({
					desc: '用于完善会员资料',
					lang: 'zh_CN',
					success(user) {
						console.log(user);
					}
				})
			},
		}
	}
</script>

<style>
	.content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;

	}

	.banner {
		width: 755rpx;
		height: 522rpx;
		margin-bottom: 60rpx;
	}

	._scroll-list {
		/* padding-bottom: 20rpx; */
		border-bottom: 20rpx solid #eee;
		width: 100%;
	}
	._scroll-head {
		display: flex;
		align-items: center;
		flex-direction: row;
		justify-content: flex-start;
		height: 100%;
		padding: 20rpx 20rpx;
	}
	
	._scroll-head ._text {
		font-size: 32rpx;
		font-weight: bold;
		color: #d6b477;
